if (typeof window !== 'undefined' && window.Vue) {
  window.Vue.component('VCircle', component.exports)
}
export default {
  name: 'VCircle',

  props: {
    position: {
      type: Object,
      required: true
    },
    radius: {
      type: [Object, Number],
      required: true
    }
  },

  computed: {
    isCircle() {
      return typeof this.radius === 'number'
    }
  }
}
